import React from "react"
import { Link } from "react-router-dom";

export default class EmbedViewer extends React.Component {
    render() {
        return (
            <div className='help-page'>
                <h1>Viewer Integration</h1>
                Integrate the viewer with your website with this step-by-step guide.
                 <h2>
                    Add the viewer to a product page
                </h2>
                <ol>
                    <li>Navigate to the <Link to='/embed'>integrate viewer page</Link> and choose the required settings. Product-specific settings can be edited in the settings tab of Ezyvue Studio.</li>
                    <li>In the HTML code section set the desired width and aspect ratio of the viewer window.</li>
                    <li>Leave the project blank in the dropdown.</li>                
                    <li>Click the Copy button and paste the code onto your page.</li>
                    <li>Change the &#60;&#60;Replace with project id&#62;&#62; with a value from your backend code.</li>
                </ol>
                <h2>
                    Add the viewer for a specific project
                </h2>
                <ol>
                    <li>Select project-specific viewer settings in Ezyvue Studio settings tab and save the scene.</li>
                    <li>Navigate to the <Link to='/embed'>integrate viewer page</Link> and choose the required settings.</li>
                    <li>In the HTML code section set the desired width and aspect ratio of the viewer window.</li>
                    <li>Select your project in the dropdown.</li>                
                    <li>Click the Copy button and paste the code onto your page.</li>
                </ol>
            </div>
        );
    }
}

